.hero-img{
    width: 100%;
    background: rgba(0,0,0,0);
    height: 60vh;
    position: relative;
}

.git-btn{
    padding:1rem;
}
.hero-img::before{
    content: "";
    background: url("https://images.unsplash.com/photo-1484417894907-623942c8ee29?q=80&w=1932&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D");
    background-size:cover;
    background-position: center top;
    height: 100%;
    width:100%;
    position:absolute;
    top: 0;
    left: 0;
    z-index:-1;
    opacity: 0.5;
}


.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-img p{
    font-size: 1.4rem;
    text-align: center;
}

.hero-img h1{
    font-size: 2.4rem;
}